  
  #notfound {
    position: relative;
    height: 100vh;
  }
  
  #notfound .notfound-bg {
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url('../../../assets/images/ErrorPage/bg.jpg');
    background-size: cover;
  }
  
  #notfound .notfound-bg:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.25);
  }
  
  #notfound .notfound {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
  
  #notfound .notfound:after {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50% , -50%);
        -ms-transform: translate(-50% , -50%);
            transform: translate(-50% , -50%);
    width: 100%;
    height: 600px;
    background-color: rgba(255, 255, 255, 0.7);
    -webkit-box-shadow: 0px 0px 0px 30px rgba(255, 255, 255, 0.7) inset;
            box-shadow: 0px 0px 0px 30px rgba(255, 255, 255, 0.7) inset;
    z-index: -1;
  }
  
  .notfound {
    max-width: 600px;
    width: 100%;
    text-align: center;
    padding: 30px;
    line-height: 1.4;
  }
  
  .notfound .notfound-404 {
    position: relative;
    height: 200px;
  }
  
  .notfound .notfound-404 h1 {
    font-family: 'Passion One', cursive;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: 220px;
    margin: 0px;
    color: #222225;
    text-transform: uppercase;
  }
  
  .notfound h2 {
    font-family: 'Muli', sans-serif;
    font-size: 26px;
    font-weight: 400;
    text-transform: uppercase;
    color: #222225;
    margin-top: 26px;
    margin-bottom: 20px;
  }
  
  .notfound-search {
    position: relative;
    padding-right: 120px;
    max-width: 420px;
    width: 100%;
    margin: 30px auto 20px;
  }
  
  .notfound-search input {
    font-family: 'Muli', sans-serif;
    width: 100%;
    height: 40px;
    padding: 3px 15px;
    color: #fff;
    font-weight: 400;
    font-size: 18px;
    background: #222225;
    border: none;
  }
  
  
  .notfound a {
    font-family: 'Muli', sans-serif;
    display: inline-block;
    font-weight: bold;
    text-decoration: none;
    background-color: transparent;
    color: #222225;
    text-transform: uppercase;
    font-size: 1.2em;
    padding: 2% 5%;
    margin-top: 10%;
  }
  

  @media only screen and (max-width: 480px) {
    .notfound .notfound-404 {
      height: 146px;
    }
  
    .notfound .notfound-404 h1 {
      font-size: 146px;
    }
  
    .notfound h2 {
      font-size: 22px;
    }
  }


