.test-history-title{
    text-align: center;
}
.test-list-history {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    width: 100%;
    padding: 1rem;
    margin: 0 auto;
    background: #F2F2F2;
    border: 1px solid #ccc;
    -webkit-box-shadow: 1px 1px 2px #fff inset, -1px -1px 2px #fff inset;
    box-shadow: 1px 1px 2px #fff inset, -1px -1px 2px #fff inset;
    border-radius: 3px/6px;
    height: calc(100vh - 150px);
    overflow-y: scroll;
}
.test-list-history::-webkit-scrollbar { 
    width: 0.4em;
}
.test-list-history::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: -webkit-linear-gradient(to bottom, #240b36, #c31432);  /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-gradient(linear, left top, left bottom, from(#240b36), to(#c31432));
    background: -webkit-linear-gradient(top, #240b36, #c31432);
    background: linear-gradient(to bottom, #240b36, #c31432); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}


